import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            items: [],
            act: 1001011,
            children: [],
            province: null,
			isHot:0,
			area:'',
        }
    },
    created(){
        this.pull(0,'')
        console.log(this.$store.state.inspctArea)
		
    },
	 mounted(){
		 if(this.$store.state.inspctArea){
			 this.area = this.$store.state.inspctArea.AreaName
		 }else{
			 this.$bridge.callhandler('pullArea','',(data) => {
				if(data){
					this.area = data
				}else{
					this.area = this.$store.state.inspctArea.AreaName
				}
			    responseCallback('接收到方法')
			})
		 }
	},
    methods:{
        choose(i,idx){
			this.act = idx
			if(idx != 1001011){
				this.$post('InspectionAPI/Censorate/QueryCensorateAreaByAreaId',{
				    AreaId: i.AreaId,
					AreaName:'',
					IsHot:-1
				}).then(o => {
				    this.children = o
				})
			}else{
				this.children = []
			}
        },
        pull(a,b){
			this.$post('InspectionAPI/Censorate/QueryCensorateAreaByAreaId',{
			    AreaId: 0,
				AreaName:'',
				IsHot:-1
			}).then(o => {
			    this.items = o
			})
        },
		toAll(){
			if(this.act == 1001011){
				this.province = {
					AreaName: '全部地区',
		            AreaId: 0
				}
			}
		    this.$store.dispatch('pushinspctArea',this.province)
		    this.$router.back()
		},
        goList(i){
            this.$store.dispatch('pushinspctArea',i)
            this.$router.back()
        }
    }
}



